body {
    background-color: #fff;
    background-size: cover; }

#company-name {
    font-size: 64px; }

h1.text-center, h3 {
    color: #800000; }

.main-top,
.main-bottom {
    padding-top: 140px;
    padding-bottom: 50px; }

.main-top {
    padding-bottom: 150px; }

#wrap {
    background-color: #fff; }

.main-button {
    font-size: 50px;
    padding: 10px 50px 10px 50px; }

input.form-control:focus {
    box-shadow: none;
    border: 1px solid #66afe9;
    height: 50px; }

input.form-control {
    height: 50px; }

.jumbotron {
    background-color: #fff;
    display: flex; }

.jumbotron__image {
    margin-top: -120px; }

.jumbotron__image > img {
    width: 450px;
    position: relative; }

.jumbotron h1 {
    font-family: 'Abril Fatface';
    color: #800000; }

.typed-cursor {
    opacity: 1;
    -webkit-animation: blink 0.7s infinite;
    -moz-animation: blink 0.7s infinite;
    animation: blink 0.7s infinite; }

.reveal-left {
    position: relative;
    animation-name: slideAndFadeIn;
    animation-duration: 1s; }

@media screen and (max-width: 900px) {

    .jumbotron {
        flex-direction: column; }


    .jumbotron__image {
        margin-top: -100px; }


    .jumbotron__image > img {
        width: 300px;
        position: relative; }


    .reveal-left {
        animation-name: slideUpAndFadeIn; } }


@keyframes slideAndFadeIn {
    from {
        opacity: 0;
        right: 100px; }


    to {
        opacity: 1;
        right: 0px; } }


@keyframes blink {
    0% {
        opacity: 1; }

    50% {
        opacity: 0; }

    100% {
        opacity: 1; } }

#login-lnk {
    color: #535353; }
